<template>
  <div id="blog-content" class="post">
    <h1 class="post-title">{{blog.title}}</h1>
    <div class="post-meta">{{blog.created_at | formatGithubTime}}<span> | </span><span class="category">
      <a href="javascript:return false;">{{blog.labels[0].name | BlogTypesFormat}}</a></span>
    </div>
    <div id="post-content"  v-html="blog.body" class="blogText post-content">
    </div>
    <div class="tags"><a href="javascript:return false;">{{blog.labels[0].name | BlogTypesFormat}}</a></div>
    <div class="post-nav">
      <a class="pre" href="javascript:return false;"></a>
      <router-link to>
      <span @click="$router.back(-1)">back</span>
      </router-link>
<!--      <a class="next" href="javascript:return false;"></a>-->
    </div>
  </div>
</template>

<script>
  import {getBlogInfo,gitapiBlogInfo} from "../api/request";
  // target.scrollIntoView();
  export default {
    name: "blogContent",
    data() {
      return {
        content:'',
        blog_id:'',
        blog: {
          title:'',
          content:'',
        },
      }
    },
    mounted() {
      // this.blog_id = this.$route.params.id;
      this.blog_id = this.$route.query.id;
      if (this.blog_id){
        console.log('mounted---'+this.blog_id)
        this.getBlogInfo()
      }
    },
    activated() {
      this.blog_id = this.$route.query.id;
      console.log('activated---'+this.blog_id)
    },
    methods:{
      getBlogInfo(){
        var that = this;
        gitapiBlogInfo(that.blog_id).then((response) => {
            that.blog = response;
        }).catch((err)=>{
        });

        // getBlogInfo(that.blog_id).then((response) => {
        //   if (response.code==200){
        //     that.blog = response.data;
        //   }
        // }).catch((err)=>{
        // });
      },
    }
  }
</script>

<style scoped>

  .blogText {
    text-align: start;
    max-width: 800px;
    overflow: hidden;
  }
  .blogText >>> h4 {
    font-weight: bold;
  }
  .blogText >>> h5 {
    font-weight: bold;
  }
  .blogText >>> h6 {
    font-weight: bold;
  }
  .blogText >>> img, p, span, div {
    /*max-width: 900px;*/
    /*max-width: 800px;*/
    /*width: 900px;*/
  }
  .tag-type{
    color: #6e7173;
    font-size: 14px;
  }

  .post {
    padding: 25px 0 15px; }
  .post .post-title {
    margin: 0;
    color: #555;
    text-align: left;
    font: bold 25px/1.1 "ff-tisa-web-pro", Cambria, "Times New Roman", Georgia, Times, sans-serif; }
  .post .post-title a {
    color: #555; }
  .post .post-meta {
    padding: 0;
    margin: 15px 0 0;
    color: #6E7173;
    float: left;
    display: inline;
    text-indent: .15em; }
  .post .post-meta:before {
    font-family: "FontAwesome";
    content: "\f073";
    padding-right: 0.3em; }
  .post .post-meta .category:before {
    font-family: "FontAwesome";
    content: "\f07c";
    padding-right: 0.3em; }
  .post .post-meta #busuanzi_value_page_pv:before {
    font-family: "FontAwesome";
    content: "\f024";
    padding-right: 0.3em; }
  .post .ds-thread-count {
    padding: 0;
    margin: 15px 0 0;
    color: #6E7173;
    float: right;
    display: inline;
    text-indent: .15em; }
  .post .ds-thread-count:before {
    font-family: "FontAwesome";
    content: "\f0e5";
    padding-right: 0.3em; }
  .post .ds-thread-count:hover {
    color: #444; }
  .post .disqus-comment-count {
    padding: 0;
    margin: 15px 0 0;
    color: #6E7173;
    float: right;
    display: inline;
    text-indent: .15em; }
  .post .disqus-comment-count:before {
    font-family: "FontAwesome";
    content: "\f0e5";
    padding-right: 0.3em; }
  .post .disqus-comment-count:hover {
    color: #444; }
  .post .post-content {
    clear: left;
    font-size: 15px;
    line-height: 1.77;
    color: #444;
    padding-top: 15px;
    text-align: justify;
    text-justify: distribute;
    word-break: break-all;
    /*overflow: hidden;*/
  }
  .post .tags {
    padding-bottom: 1em;
    text-align: start;
  }
  .post .tags a {
    margin-right: .5em;
  }
  .post .tags a:before {
    font-family: "FontAwesome";
    content: "\f0c6";
    padding-right: 0.3em;
  }
  #blog-content a {
    color: #6e7173;
  }
  #blog-content a:hover {
    color: #555;
  }
  #blog-content a:active {
    color: #444;
  }


  #post-content p a {
    color: #6e7173;
  }
  #post-content p  a:hover {
    color: #555;
  }
  #post-content p  a:active {
    color: #444;
  }
  #post-content h3,h4,h5{
    font-weight: 600;
  }
</style>



